*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
  font-family: "Roboto", sans-serif;
}

.wrapperTODO {
  width: 144rem;
  margin: 0 auto;
}

.createTaskWrapper {
  font-size: 1.6rem;
  display: flex;
  align-items: center;

  justify-content: center;
}

.createTaskInput {
  padding: 0.5rem 0.3rem;
  font-size: 1.6rem;
  margin: 0.2rem 0.5rem;
}

.labelInputCreateToDoWrapper {
  display: flex;
  align-items: center;
}

.js-btn-addToDo {
  padding: 0.6rem 1rem;
  font-size: 1.4rem;
  text-transform: uppercase;
}

.active {
  text-decoration: line-through;
}

.wrapperToDoList {
  margin: 0 auto;
}

.wrapperTodos {
  margin: 0 auto;
}

.itemToDos {
  margin: 1rem auto;
  display: flex;
  justify-content: space-around;
  align-items: center;

  border: 1px solid #333;
  width: 25%;
  height: 5rem;
}

.checkboxToDo {
  margin: 1rem;
}

.js-btn-delete {
  margin: 1rem;
  font-size: 1.6rem;
  padding: 0.2rem 0.6rem;
  display: flex;
  align-items: center;
  justify-content: center;

  color: #fff;
  background-color: red;

  border-radius: 50%;
  border: none;

  border-bottom: 3px solid #333;
  transition: 0.2s all ease;
  cursor: pointer;

  &:active {
    border-bottom: none;
  }
}

.inputTask {
  border: none;
  cursor: pointer;

  font-size: 1.7rem;
  padding: 0.5rem 1rem;
  outline: none;
  transition: 0.2s all ease;

  &:hover {
    color: rgb(255, 230, 0);
    background-color: cadetblue;
  }

  &:focus {
    border: 1px solid green;
  }

  &:disabled {
    background-color: rgb(170, 170, 170);
  }
}

.labelSearch {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;

  width: 25%;
  margin: 0 auto;
}

.filterInputInfo {
  margin: 1rem auto;
  padding: 0.5rem 1rem;
  font-size: 1.7rem;
}
